<template>
  <div>
    <nav-bar :name="title"></nav-bar>
<v-card class="ma-1" cols="12">
<v-container>
  <v-row>
    <v-col cols="6">
      <v-text-field
      class="mb-n5"
      outlined
      dense
      v-model="user.firstname"
      label="First name*"
      required
      ></v-text-field>
    </v-col>
    <v-col cols="6">
      <v-text-field
      class="mb-n5"
      outlined
      dense
      v-model="user.lastname"
      label="Last name*"
      required
      ></v-text-field>
    </v-col>
    <v-col cols="8">
      <v-file-input
      class="mb-n5"
      label="Choose file"
      outlined
      dense
      v-model="user.img"
      ></v-file-input>
    </v-col>
    <v-col cols="4">
      <v-checkbox
    class="mb-n5 mt-0"
    outlined
    dense
      v-model="user.staff"
      label="Is staff"
      required
      v-if="isStaff"
      ></v-checkbox>
    </v-col>
    <v-col cols="12">
      <v-text-field
    class="mb-n5"
    outlined
    dense
      v-model="user.username"
      label="Username*"
      required
      ></v-text-field>
    </v-col>
    <v-col cols="6">
      <v-text-field
    class="mb-n5"
    outlined
    dense
      v-model="user.phone"
      label="Phone number"
      required
      ></v-text-field>
    </v-col>
    <v-col cols="6">
      <v-text-field
    class="mb-n5"
    outlined
    dense
      v-model="user.email"
      label="Email address*"
      required
      ></v-text-field>
    </v-col>
    <v-col>
    <v-btn
      class="mx-4 my-2"
      color="#989898"
      dark
      @click="$router.go(-1)"
    >
      GO BACK
    </v-btn>
    <v-btn
      class="mx-4 my-2"
      color="#E47911"
      dark
      @click="update_user()"
    >
      UPDATE USER
    </v-btn>
    </v-col>
  </v-row>
</v-container>
</v-card>
  </div>
</template>

<script>
  import NavBar from '@/components/NavBar';
  import UserService from '@/services/UserService'
  import Swal from 'sweetalert2'

  export default {
    components: {
      NavBar
    },
    data: () => ({
      title: 'UPDATE USER',
      user:{
        firstname: '',
        lastname: '',
        img: null,
        username: '',
        phone: '',
        email: '',
        staff: false,
      }
    }),
    computed:{
        unique_id(){
            return this.$route.params.id
        },

        isStaff(){
            if (this.$store.state.auth.user.is_staff)
            return true;

            return false;
        },
    },

    mounted(){
        if(this.isStaff){
            UserService
                .get(this.unique_id)
                .then(res => {
                    this.user.firstname = res.first_name
                    this.user.lastname = res.last_name
                    this.user.username = res.username
                    this.user.phone = res.phone_number
                    this.user.email = res.email
                    this.user.staff = res.is_staff
                })
                .catch(e => {
              let error = e.response.data;
              let msg = ''
              Object.entries(error).forEach(([ key, value ]) => {
                msg+= key + ': ' + value + ' \n ';
              });
                Swal.fire({
                icon: 'error',
                title: 'Error',
                text: msg
                });
            })
        }
        else{
            UserService
            .get_profile()
            .then(res => {
                this.user.firstname = res.first_name
                this.user.lastname = res.last_name
                this.user.username = res.username
                this.user.phone = res.phone_number
                this.user.email = res.email
                this.user.staff = res.is_staff
            })
            .catch(e => {
            let error = e.response.data;
            let msg = ''
            Object.entries(error).forEach(([ key, value ]) => {
              msg+= key + ': ' + value + ' \n ';
            });
              Swal.fire({
              icon: 'error',
              title: 'Error',
              text: msg
              });
          })
        }
    },

    methods:{
      update_user(){
          if(this.isStaff){
              UserService
          .update(this.user, this.unique_id)
          .then(() => {
              Swal.fire({
                  icon: 'success',
                  title: 'Success',
                  text: 'The user successfully updated.'
              }).then(() => {
                  this.$router.go(-1)
                })
          })
          .catch(e => {
            let error = e.response.data;
            let msg = ''
            Object.entries(error).forEach(([ key, value ]) => {
              msg+= key + ': ' + value + ' \n ';
            });
              Swal.fire({
              icon: 'error',
              title: 'Error',
              text: msg
              });
          })
        }
        else{
            UserService
            .update_profile(this.user, this.unique_id)
            .then(() => {
                Swal.fire({
                    icon: 'success',
                    title: 'Success',
                    text: 'The user successfully updated.'
                }).then(() => {
                    this.$router.go(-1)
                })
            })
            .catch(e => {
            let error = e.response.data;
            let msg = ''
            Object.entries(error).forEach(([ key, value ]) => {
              msg+= key + ': ' + value + ' \n ';
            });
              Swal.fire({
              icon: 'error',
              title: 'Error',
              text: msg
              });
          })
        }
      },

    }
  }
</script>